
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

import Tabbar from '@/components/Public/Tabbar'
export default {
components: {
Tabbar, Tab, TabItem, Flexbox, FlexboxItem, Scroller, Group, Grid, GridItem
},
data () {
return {
list: [],
useTools: [],
img: 'http://thumb.niutuku.com/960x1/8f/b8/8fb8fb2623afa6336e2be205718f5f0e.jpg',
itemList: [
{ name: '', src: require('@/assets/icon/more.png') },
{ name: '', src: require('@/assets/icon/traffic.png') },
{ name: '', src: require('@/assets/icon/marry.png') },
{ name: '', src: require('@/assets/icon/company.png') },
{ name: '', src: require('@/assets/icon/home.png') },
{ name: '', src: require('@/assets/icon/contract.png') },
{ name: '', src: require('@/assets/icon/right.png') },
{ name: '', src: require('@/assets/icon/work.png') },
{ name: '', src: require('@/assets/icon/police.png') },
{ name: '', src: require('@/assets/icon/litigation.png') }
]
}
},
mounted () {

this.weekSort()
},
methods: {
onItemClick: function (index) {
},
weekSort: function () {
this.$getData(this.$configs.api.news, `?page=1&pageSize=10`, response => {
const list = response.list
this.list = list
})
}
}
}



